
import { ref, toRefs } from 'vue';
import { debounce } from 'lodash';
import { ElMessage } from 'element-plus';
import useClipboard from 'vue-clipboard3';
import { useFavoriteMutation } from '@/graphql';

export default {
  props: {
    movie: Object
  },

  setup(props) {
    const { movie } = toRefs(props);
    const favorited = ref(movie.value?.favorited);
    const link = movie.value?.id
      ? `#${movie.value?.name ?? movie.value?.introduction}#` +
        `https://juhaokantv.com/movie/${movie.value?.id}`
      : '';
    const movieLink = ref(link);

    const { toggleFavorite, onDone } = useFavoriteMutation(
      { id: movie.value.id, type: 'movies' },
      { fetchPolicy: 'network-only' }
    );

    onDone(({ data }) => {
      if (data) {
        favorited.value = data?.toggleFavorite?.favorited;
        ElMessage({
          message: `${data?.toggleFavorite?.favorited ? '!' : ''}`,
          type: 'success'
        });
      }
    });

    const favoriteHandler = debounce(() => {
      favorited.value = !favorited.value;
      toggleFavorite();
    }, 500);

    const { toClipboard } = useClipboard();
    async function copyMovieLink() {
      if (movieLink.value) {
        try {
          await toClipboard(movieLink.value);
          ElMessage({ message: '', type: 'success' });
        } catch (error) {
          ElMessage({ message: '', type: 'error' });
        }
      } else {
        ElMessage({ message: '', type: 'error' });
      }
    }

    function watchTogether() {
      ElMessage({
        message: '',
        type: 'info'
      });
    }

    return {
      movieLink,
      favorited,
      favoriteHandler,
      copyMovieLink,
      watchTogether
    };
  },
  methods: {
    showReportModal() {
      this.$bus.emit('SHOW_REPORT_MODAL', { id: this.movie.id, title: '' });
    },
    /* eslint-disable */
    downloadApp() {
      ULink([
        {
          id: 'usr1kj7mbfbqffk5', // LinkID
          data: {
            // 
            movie_id: this.movie.id,
            route: 'MovieDetail'
          },
          // selector: '#xxl', //
          // U-Link API
          auto: true,
          timeout: 2000,
          lazy: false
        }
      ]);
    }
  }
};
